<template>
  <div class="product-detail">
    <el-dialog title="Message" :visible.sync="dialogVisible" width="90%">
      <!-- :before-close="handleClose" -->
      <div class="price">
        Pre-payment
        <span>₹{{charge-handNumber*39}}</span>
      </div>
      <div class="price">
        Service Fee
        <span>₹{{handNumber*39}}</span>
      </div>
      <div class="price">
        Order price
        <span class="gold">₹256</span>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">Cancel</el-button>
        <el-button type="primary" @click="handlePay">OK</el-button>
      </span>
    </el-dialog>
    <s-header :name="'Smart Buy Gold Bar'"></s-header>
    <div class="detail-content">
      <div class="detail-swipe-wrap">
        <van-swipe class="my-swipe" indicator-color="#1baeae">
          <van-swipe-item v-for="(item, index) in detail.goodsCarouselList" :key="index">
            <img :src="prefix(item)" alt />
          </van-swipe-item>
        </van-swipe>
      </div>
      <div class="product-info">
        <div class="product-title">{{ detail.goodsName }}</div>
        <div class="product-price">
          <span>₹{{ detail.sellingPrice }}</span>
          <!-- <span>库存203</span> -->
        </div>
      </div>
      <ul class="product-contents">
        <li class="content-free">
          <img src="https://wap.aofygold.in/common/images/freeShip.png" alt />
          &nbsp;Free shipping in India
        </li>
        <li class="content-free">
          Real-time price(Rs/gm)：
          <span>₹6466.54</span>
        </li>
        <li class="content-free">
          Pre-payment：
          <span>₹{{charge}}</span>
        </li>
      </ul>
      <div class="content-weight">Gold Weight:</div>
      <div class="weight-detail">
        <div
          class="detail-w"
          @click="handleClick(item.id)"
          :class="weightIndex===item.id?'color':''"
          v-for="item in weight"
          :key="item.id"
        >{{item.w}}gm</div>
      </div>
      <div class="content-weight">Volume Purchase:</div>
      <div class="weight-detail">
        <div
          class="detail-p"
          @click="handlePiece(item.id)"
          :class="pieceIndex===item.id?'color':''"
          v-for="item in pieces"
          :key="item.id"
        >{{item.p}} &nbsp;pieces</div>
      </div>
      <div class="content-weight">Quantity:</div>
      <div class="quantity">
        <span @click="handleChange(-1)">一</span>
        <span>{{piece}}</span>
        <span @click="handleChange(1)">+</span> &nbsp;&nbsp;In stock:&nbsp;1680
      </div>
      <div class="product-intro">
        <!-- <ul>
          <li>概述</li>
          <li>参数</li>
          <li>安装服务</li>
          <li>常见问题</li>
        </ul>-->
        <div class="product-content" v-html="detail.goodsDetailContent"></div>
      </div>
    </div>
    <van-goods-action>
      <van-goods-action-icon icon="chat-o" text="Service" />
      <van-goods-action-icon icon="cart-o" :info="!count ? '' : count" @click="goTo()" text="Cart" />
      <van-goods-action-button type="warning" @click="getDialog" text="Buy now" />
      <van-goods-action-button class="gray" type="danger" @click="getDialog" text="Smart Buy" />
    </van-goods-action>
  </div>
</template>

<script>
import { getDetail } from "../service/good";
import { addCart } from "../service/cart";
import sHeader from "@/components/SimpleHeader";
import { getRest, insertShop } from "../service/golddetail";
import { Toast } from "vant";
import { getLocal } from "../common/js/utils";
export default {
  data() {
    return {
      id: 0,
      dialogVisible: false,
      weightIndex: 0,
      pieceIndex: -1,
      piece: 1,
      weight: [
        {
          id: 0,
          w: 10
        },
        {
          id: 1,
          w: 50
        },
        {
          id: 2,
          w: 100
        },
        {
          id: 3,
          w: 500
        },
        {
          id: 4,
          w: 1000
        },
        {
          id: 5,
          w: 3000
        }
      ],
      pieces: [
        {
          id: 0,
          p: 3
        },
        {
          id: 1,
          p: 5
        },
        {
          id: 2,
          p: 10
        },
        {
          id: 3,
          p: 20
        }
      ],
      detail: {
        goodsCarouselList: []
      }
    };
  },
  components: {
    sHeader
  },
  async mounted() {
    const { id } = this.$route.params;
    this.id = id;
    const { data } = await getDetail(id);
    this.detail = data;
  },

  methods: {
    async handlePay() {
      this.dialogVisible = false;
      let token = getLocal("token");
      // console.log(token);
      const { data } = await getRest();
      // console.log(data);
      // const res = await insertShop({
      //   "buyingPoint": 10,
      //   "goodId": this.id,
      //   "ops": this.handNumber
      // })
      // console.log(res);
      // return;
      // if()
      if(data.rest>300*this.handNumber){
           insertShop({
        "buyingPoint": 300,
        "goodId": this.id,
        "ops": this.handNumber,
      }).then(res=>{
        if(res.resultCode==200){
           this.$toast.show("Payment success",1500)
        }
       
      });
      }else{
            this.$toast.show("Insufficient Balance",1500)
            //  this.route
        }
    

      // http://192.168.1.103
    },
    handleChange(change) {
      if (change == -1) {
        if (this.piece > 1) {
          this.piece--;
        }
      } else if (change == 1) {
        this.piece++;
      }
      if (this.piece == 3) {
        this.pieceIndex = 0;
      } else if (this.piece == 5) {
        this.pieceIndex = 1;
      } else if (this.piece == 10) {
        this.pieceIndex = 2;
      } else if (this.piece == 20) {
        this.pieceIndex = 3;
      } else {
        this.pieceIndex = -1;
      }
    },
    handlePiece(id) {
      this.pieceIndex = id;
      this.piece = this.pieces[id].p;
      // console.log(this.piece);
    },
    handleClick(id) {
      this.weightIndex = id;
    },
    goBack() {
      this.$router.go(-1);
    },
    goTo() {
      this.$router.push({ path: "/cart" });
    },
    async addCart() {
      const { data, resultCode } = await addCart({
        goodsCount: 1,
        goodsId: this.detail.goodsId
      });
      if (resultCode == 200) Toast.success("添加成功");
      this.$store.dispatch("updateCart");
    },
    // async goToCart() {
    //   const { data, resultCode } = await addCart({
    //     goodsCount: 1,
    //     goodsId: this.detail.goodsId
    //   });
    //   this.$store.dispatch("updateCart");
    //   this.$router.push({ path: "/cart" });
    // },
    getDialog() {
      // console.log("点击");
      this.dialogVisible = true;
    }
  },
  computed: {
    // 手续费
    charge() {
      return this.piece * this.weight[this.weightIndex].w * 30;
    },
    // 手数
    handNumber() {
      return (this.piece * this.weight[this.weightIndex].w) / 10;
    },
    count() {
      return this.$store.state.cartCount;
    }
  }
};
</script>

<style lang="less">
@import "../common/style/mixin";
.product-detail {
  .el-dialog__wrapper {
    .el-dialog {
      .el-dialog__body {
        .price {
          display: flex;
          justify-content: space-between;
          padding: 15px;
          font-weight: 600;
          border-bottom: 1px solid #aaaaaa;
          span {
            display: inline-block;
            margin-right: 5%;
          }
          .gold {
            color: #c7b39a;
          }
        }
      }
    }
  }
  .detail-header {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    .fj();
    .wh(100%, 44px);
    line-height: 44px;
    padding: 0 10px;
    .boxSizing();
    color: #252525;
    background: #fff;
    border-bottom: 1px solid #dcdcdc;
    .product-name {
      font-size: 14px;
    }
  }
  .detail-content {
    margin-top: 44px;
    .detail-swipe-wrap {
      .my-swipe .van-swipe-item {
        img {
          width: 100%;
          // height: 300px;
        }
      }
    }
    .product-info {
      padding: 0 10px;
      .product-title {
        font-weight: 600;
        font-size: 18px;
        text-align: left;
        color: #333;
        margin-bottom: 16px;
      }
      .product-desc {
        font-size: 14px;
        text-align: left;
        color: #999;
        padding: 5px 0;
      }
      .product-price {
        .fj();
        span:nth-child(1) {
          color: #f39839;
          font-size: 24px;
        }
        span:nth-child(2) {
          color: #999;
          font-size: 16px;
        }
      }
    }
    .product-contents {
      .content-free {
        padding: 6px 5px;
        span {
          color: #f39a44;
          font-weight: 700;
        }
      }
      li {
        img {
          width: 15px;
          height: 15px;
        }
      }
    }
    .content-weight {
      font-size: 16px;
      font-weight: 600;
      padding: 10px 5px;
    }
    .quantity {
      // text-align: center;
      line-height: 30px;
      span {
        display: inline-block;
        margin-left: 4%;
        line-height: 30px;
        width: 15%;
        font-weight: 700;
        text-align: center;
        background-color: #f2f2f2;
      }
    }
    .weight-detail {
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      .detail-w {
        margin: 5px 0;
        width: 30%;
        line-height: 30px;
        text-align: center;
        background-color: #f2f2f2;
      }
      .detail-p {
        margin: 5px 0;
        width: 20%;
        line-height: 30px;
        text-align: center;
        background-color: #f2f2f2;
      }
      .color {
        background-color: #13347b;
        color: #fff;
        font-weight: 600;
      }
    }
    .product-intro {
      width: 100%;
      ul {
        .fj();
        width: 100%;
        margin: 10px 0;
        li {
          flex: 1;
          padding: 5px 0;
          text-align: center;
          font-size: 15px;
          border-right: 1px solid #999;
          box-sizing: border-box;
          &:last-child {
            border-right: none;
          }
        }
      }
      .product-content {
        padding: 0 20px;
        img {
          width: 100%;
        }
      }
    }
  }
  .van-goods-action-button--warning {
    background: linear-gradient(to right, #ff0000, pink);
  }
  .van-goods-action-button--danger {
    background: linear-gradient(to right, pink, #ff0000);
  }
}
</style>
